.form-input {
  font-family: "Lato", sans-serif;
  color: white;
  font-size: 1em;
  width: 100%;
  margin-bottom: 3em;
  background-color: #a38b8b;
  outline: none;
  border: none;
  padding: 0.5em;
}

.form-input:focus {
  font-family: "Lato", sans-serif;
  color: white;
  font-size: 1em;
  width: 100%;
  margin-bottom: 3em;
  background-color: #b4a2a2;
  outline: none;
  border: none;
  padding: 0.5em;
  border-bottom: white 2px solid;
}

.recipient-container {
  display: flex;
  margin-bottom: 3em;
}

.recipient-label {
  display: block;
  font-family: "Lato", sans-serif;
  color: white;
  font-size: 1em;
}

.recipient-list {
  display: block;
  opacity: .7;
  text-align: center;
  font-family: "Lato", sans-serif;
  color: white;
  font-size: 1em;
  margin-bottom: 3em;
}

.recipient-checkbox {
  display: block;
  margin-right: 1em;
}

.addAddendums {
  font-family: "Lato", sans-serif;
  color: white;
  font-size: 1em;
  width: 100%;
  height: 30vh;
  margin-bottom: 3em;
  background-color: #a38b8b;
  outline: none;
  border: none;
  padding: 0.5em;
}

.addAddendums:focus {
  font-family: "Lato", sans-serif;
  color: white;
  font-size: 1em;
  width: 100%;
  height: 30vh;
  margin-bottom: 3em;
  background-color: #9b8c8c;
  outline: none;
  border-bottom: white 2px solid;
  padding: 0.5em;
}

.nextButton {
  background-color: #4aacc5;
  outline: none;
  border: none;
  padding: 0.6em 1.2em;
  -ms-grid-row-align: center;
  align-self: center;
  border-radius: 5px;
  color: #e7dbdb;
  font-size: 1.1em;
}

.nextButton:disabled {
  background-color: #3d646e;
  outline: none;
  border: none;
  padding: 0.6em 1.2em;
  -ms-grid-row-align: center;
  align-self: center;
  border-radius: 5px;
  color: #c2bfbf;
  font-size: 1.1em;
}

.contractContainer, .no-form {
  display: flex;
  flex-direction: column;
}

.no-form {
  margin-top: 5em;
}

.cantractTitle {
  color: white;
  font-family: "Roboto", sans-serif;
  font-size: 2em;
  text-align: center;
  margin-bottom: 1.3em;
  margin-top: 0.2em;
}

.companyLogo {
  width: 50%;
  -ms-grid-row-align: center;
  align-self: center;
}

.progressContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 2em;
}

.proDotInactive {
  display: inline;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background-color: #8b7575;
  margin: 0 1em;
  cursor: pointer;
}

.proDotActive {
  display: inline;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background-color: #ffffff;
  margin: 0 1em;
  cursor: pointer;
}

@media only screen and (min-width: 600px) {
  .companyLogo {
    width: 30%;
    -ms-grid-row-align: center;
    align-self: center;
  }
  .no-form {
    margin-top: 15em;
  }
}

@media only screen and (min-width: 1000px) {
  .companyLogo {
    width: 15%;
    -ms-grid-row-align: center;
    align-self: center;
  }
}
